$theme-name: ddl-v2;

:root {

  /* --------- COLOURS --------- */

  --white: #ffffff;
  --black: #3A4145;
  --grey: #ededed;
  --grey-dark: #505050;
  --grey-light: #f6f6f6;
  --accent: #1171ae;
  --accent-light: #73aed3;
  --danger: #b81c23;

  /* --------- GLOBAL --------- */

  --body-font:  'Urbanist';

  --body-bg: var(--black);
  --main-bg: var(--white);
  
  --regular: 300;
  --medium: 500;
  --bold: 700;

}


/* --------- MAPS --------- */

$colors: (
  "grey": var(--grey),
  "grey-light": var(--grey-light),
  "grey-dark": var(--grey-dark),
  "accent": var(--accent),
  "accent-light": var(--accent-light),
  "white": var(--white),
  "black": var(--black)
);